export default function addForm(params) {
  let data = {
    name: '',
    price: '',
  };
  if (params.name && params.price) {
    data = params;
  }

  const element = document.createElement('div');
  element.classList.add('form-holder');
  element.innerHTML = `
  <form id="goodsForm" novalidate class="form">
    <h3 class="form__input-header">–ù–∞–∑–≤–∞–Ω–∏–µ</h3>
    <div class="form-control">
      <input class="form__input-input" type="text" id="name" name="name" value="${data.name}" required placeholder="–í–≤–µ–¥–∏—Ç–µ –Ω–∞–∑–≤–∞–Ω–∏–µ" >
    </div>
    <h3 class="form__input-header">–°—Ç–æ–∏–º–æ—Å—Ç—å</h3>
    <div class="form-control">
      <input class="form__input-input" type="text" id="price" name="price" value="${data.price}" required placeholder="–í–≤–µ–¥–∏—Ç–µ —Å—Ç–æ–∏–º–æ—Å—Ç—å (123.45)" >
    </div>
    <div class="form__input-button-holder">
      <button type="submit" class="btn" id="save">–°–æ—Ö—Ä–∞–Ω–∏—Ç—å</button>
      <button type="button" class="btn" id="cancel">–û—Ç–º–µ–Ω–∞</button>
    </div>
  </form>
  `;

  return element;
}
